.App {
    font-family: Arial;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: #64dcfe;

}

.App_wrapper {
    width: 800px;
    margin: 0 auto;
    margin-top: 20px;
    background-color: #2a2c36;
    padding: 20px;
}


.active {
    background-color: red
}

.settings_wrapper {
    display: flex;
    flex-direction: column;
    width: 350px;
    align-items: center;
    border: solid #64dcfe 2px;
    justify-content: space-around;
    border-radius: 10px;
    height: 250px;
}

.settings_display {
    width: 300px;
    height: 100px;
    border: solid #64dcfe 2px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border-radius: 10px;
    margin: 20px;
    padding: 30px;
    background-color: #2a2c36;
    box-sizing: border-box;

}

.settings_button {
    border: solid #64dcfe 2px;
    width: 300px;
    text-align: center;
    margin: 20px;
    padding: 20px;
    border-radius: 10px;
    background-color: #2a2c36;
    box-sizing: border-box;

}

.settings_display-maxValue {
    width: 250px;
    display: flex;
    justify-content: space-between;
    margin: 10px;
    align-items: center;

}

.settings_display-maxValue input {
    width: 120px;
    border: solid #64dcfe 2px;
    border-radius: 3px;
    padding: 2px;
    text-align: center;
    height: 20px;

}

.settings_display-startvalue input {
    width: 120px;
    border: solid #64dcfe 2px;
    border-radius: 3px;
    padding: 2px;
    text-align: center;
    height: 20px
}

.settings_display-startvalue {
    width: 250px;
    display: flex;
    justify-content: space-between;
    margin: 10px;
    align-items: center;
}

button {
    padding-top: 5px;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 5px;
    font-family: inherit;
    font-weight: bold;
    font-size: 20px;
    color: black;
    background-color: #64dcfe;
    border: none;
}

button:disabled {
    background-color: rgba(100, 220, 254, 0.2);
}


.counter_wrapper {
    display: flex;
    flex-direction: column;
    width: 350px;
    align-items: center;
    border: solid #64dcfe 2px;
    justify-content: space-around;
    border-radius: 10px;
    height: 250px;
    box-sizing: border-box;
}

.counter_wrapper input {
    height: 100px;
    width: 300px;
    box-sizing: border-box;
    margin: 20px;
    border: solid #64dcfe 2px;
    border-radius: 10px;
    background-color: #2a2c36;
    color: #64dcfe;
    text-align: center;
    font-weight: bold;
    font-size: 50px;
}
input.inputError{
    border-color: red;
    background-color:rgba(240,155,174,0.8)
}


input.activeBig{color: red;font-size: 50px; font-weight: bold}
input.active{color: red;font-size: 20px}
input.valueError{color: red; font-size: 40px; }
input.valueInput{font-size: 20px}


.buttonsWrapperInner{
    display: flex;
    justify-content: space-around;
}

.counter_buttonsWrapper {
    border: solid #64dcfe 2px;
    border-radius: 10px;
    width: 300px;
    text-align: center;
    margin: 20px;
    padding: 20px;
    background-color: #2a2c36;
    box-sizing: border-box;
}
label{font-weight: bold}


